  export default {
    name: 'AddressIndex',
    data (){
      return {
        checkedTrue: true,
        checkedFalse: false,
        list: []
      }
    },
    methods: {
      //地址选择
      checkAddress(val){
        let address = JSON.stringify(val)
        let goods_id = this.$route.query.goods_id
        let key = this.$route.query.key
        let count = this.$route.query.count
        this.$router.push({path: '/order/orders',query:{
          goods_id: goods_id,
          key: key,
          count: count,
          address: address
        }})
      },
      //删除
      deleteAddress(id,index){
        this.http.delete(this.ports.me.address+'/'+id, res =>{
          console.log(res)
          if(res.success){
            this.list.splice(index, 1)
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      },
      //设置默认地址
      defaultClick(item){
        this.list.forEach((even,i)=>{
          even.is_default = 0
        })
        item.is_default = 1
        this.http.put(this.ports.me.address+'/'+item.id,{is_default: 1}, res =>{
          if(res.success){
//            let data = res.data
//            this.list = data.res
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      },
      showToastTxtOnly(text) {
        this.toast = this.$createToast({
          txt: text,
          type: 'txt'
        })
        this.toast.show()
      },
      //获取列表数据
      getAddressData(){
        this.http.get(this.ports.me.address, res =>{
          this.$store.commit('changeLoading',false)
          console.log(res)
          if(res.success){
            let data = res.data
            this.list = data.res
          }else{
//            this.showToastTxtOnly(res.msg)
            this.list = []
          }
        })
      }
    },
    mounted (){
      this.getAddressData()
    }
  }



